@import "../config/colors";
@import "../config/breakpoints";

.proposal {
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.5);
  border-radius: 10px;
  margin: 8px 0;
  padding: 8px;
  width: 100%; }

.proposal__buttons {
  display: flex;
  flex-direction: column;
  align-items: center; }

.proposal__button {
  background: none;
  border: none;
  cursor: pointer;
  margin: 4px 24px; }

.proposal__info {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  flex-grow: 1;
  margin-right: 32px;
  margin-left: 8px;
  width: 80%; }

.proposal__name {
  font-size: 18px;
  margin-top: 4px;
  margin-bottom: 8px;
  font-weight: 300;
  overflow-wrap: break-word;
  width: 100%; }

.proposal__description {
  margin: 0;
  margin-bottom: 8px;
  color: $color-dark-grey;
  font-size: 14px;
  overflow-wrap: break-word;
  max-width: 100%; }

.proposal__result {
  background: none;
  border: none;
  color: $color-brand;
  font-size: 14px;
  align-self: flex-end;
  margin-bottom: 8px;
  cursor: pointer; }

.proposal__time {
  font-size: 14px;
  color: $color-grey;
  margin: 0;
  align-self: flex-end;
  margin-bottom: 8px;
  width: 100px; }

.proposal__results {
  color: $color-grey;
  font-size: 14px;
  margin-bottom: 8px;
  align-self: flex-end;
  padding-right: 4px; }

.hidden {
  visibility: hidden;
  opacity: 0; }

.gone {
  display: none; }
